body {
  background-color: #f4f4f4;
}

.dark {
  background-color: #686868;
}

.switch {
  width: 40px;
  height: 20px;
  font-size: 17px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
  display: none;
}

.slider {
  cursor: pointer;
  background-color: #f4f4f4;
  border: 1px solid #4440f6;
  border-radius: 30px;
  transition: all .2s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #4440f6;
  border-radius: 20px;
  transition: all .5s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input:checked + .slider:before {
  background-color: #f4f4f4;
}

input:checked + .slider {
  background-color: #4440f6;
  border: 1px solid #f4f4f4;
}

input:focus + .slider {
  box-shadow: 0 0 1px #4440f6;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.theme-switcher-container {
  display: none;
}

@media screen and (min-width: 768px) {
  .theme-switcher-container {
    width: 98px;
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .theme-switcher-container {
    width: 148px;
  }
}

.theme-switcher-text {
  display: none;
}

@media screen and (min-width: 1280px) {
  .theme-switcher-text {
    font-family: Poppins;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.33;
    display: block;
  }
}

.theme-switcher-text.light {
  color: #4440f6;
}

.dark .theme-switcher-text.light {
  color: #f4f4f4;
}

.theme-switcher-text.dark {
  background-color: #f4f4f4;
}

.dark .theme-switcher-text.dark {
  color: #4440f6;
  background-color: #686868;
}

.theme-switcher-svg {
  display: none;
}

@media screen and (min-width: 768px) {
  .theme-switcher-svg {
    display: block;
  }
}

@media screen and (min-width: 1280px) {
  .theme-switcher-svg {
    display: none;
  }
}

.theme-switcher-svg.light {
  stroke: #4440f6;
}

.dark .theme-switcher-svg.light {
  stroke: #f4f4f4;
}

.theme-switcher-svg.dark {
  stroke: #5f6775;
  background-color: #f4f4f4;
